import axios from "axios";
import React, { Component } from "react";
import Book from "./book/book";
import s from "./content.module.css";

class Content extends Component {
  state = {
    startUrl: 82535,
    books: [],
    initialized: false,
    search: []
  };

  componentDidMount() {
    let temp = this.state.startUrl;

    for (let i = 0; i < 60; i++) {
      ((temp)=>{
        axios
        .get(`https://openlibrary.org/works/OL${temp}W.json`)
        .then((response) => {
          response.data.covers &&
            this.setState({
              books: [
                ...this.state.books,
                {
                  title: response.data.title,
                  imgUrl: `https://covers.openlibrary.org/b/id/${response.data.covers[0]}-M.jpg`,
                  bookId: temp,
                },
              ],
            });
        }).catch()
      })(temp)
      temp += 1;
    }
    this.setState({ initialized: true });
    

  }

  render() {
    return (
      <div className={s.content}>
        {this.state.initialized &&
          this.state.books.map((item, index) => (
            <Book key={index} data={item} />
          ))}
      </div>
    );
  }
}

export default Content;
