import React, { Component } from "react";
import s from "./header.module.css";
import bookUrl from "../../images/books.png";
import sunUrl from "../../images/sun.svg";
import moonUrl from "../../images/moon.svg";
import { NavLink } from "react-router-dom";

const Header = () => {
  return (
    <header className={s.header + " container p-2 d-md-flex"}>
      <div className="col-md-4 col-8 offset-2 offset-md-0 text-center d-flex justify-content-center mb-2 mb-md-0">
        <div>
          <img
            src={bookUrl}
            alt="Some text"
            className={s.bookLogo + " img-fluid"}
          />
        </div>
        <div className={s.name + " fs-4"}>Simple Library</div>
      </div>
      <SearchField />
      <div className="col-md-4 col-8 offset-2 offset-md-0">
        <span>
          <img src={sunUrl} alt="Light" className={s.icon} />
          <img src={moonUrl} alt="Dark" className={s.icon} />
        </span>
      </div>
    </header>
  );
};

class SearchField extends Component {
  state = {
    field: "",
  };

  onInputChange = (e) => {
    this.setState({field: e.target.value})
  }

  render() {
    return (
      <div className="col-md-4 col-10 offset-1 offset-md-0">
        <div className="input-group">
          <input
          value = {this.state.field}
          onChange = {this.onInputChange}
            type="text"
            className="form-control"
            placeholder="Harry Potter"
            aria-label="Recipient's username"
            aria-describedby="button-addon2"
          />
            <NavLink to={`/search/${this.state.field.split(' ').join('+')}`} className='btn btn-primary' type="button" id="button-addon2">Search</NavLink>
        </div>
      </div>
    );
  }
}

export default Header;
