import axios from "axios";
import React, { Component } from "react";
import { withRouter } from "react-router";
import s from "./about-book.module.css";
import defaultCover from '../../images/bookCoverDefault.jpg'

class AboutBook extends Component {
  state = {
    cover: null,
    author: null,
    description: "",
    title: "",
    id: null,
    authorPhotoId: null,
    authorName: null,
  };

  componentDidMount() {
    axios
      .get(
        `https://openlibrary.org/works/OL${this.props.match.params.bookId}W.json`
      )
      .then((response) => response.data)
      .then((data) => {
        this.setState({
          title: data.title,
          description: data.description,
          id: this.props.match.params.bookId,
          author: data.authors[0].author.key,
          cover: data?.covers && data.covers[0],
        });
        axios
          .get(`https://openlibrary.org${this.state.author}.json`)
          .then((response) => response.data)
          .then((data) =>
            this.setState({
              authorPhotoId: data?.photos ? data.photos[0] : null,
              authorName: data.name,
            })
          );
      });
  }

  render() {
    return (
      <div className={s.content + " container d-flex p-2"}>
        <div className="col-3 mt-3">
          <img
            src={this.state.cover ? `https://covers.openlibrary.org/b/id/${this.state.cover}-L.jpg` : defaultCover}
            alt="Sorry, there is no cover image"
            className={s.coverPhoto + " img-fluid"}
          />
          <div className="mt-3">
            <a
              href={`https://openlibrary.org/works/OL${this.props.match.params.bookId}W`}
              target="new"
            >
              Original Site
            </a>
          </div>
        </div>
        <div className="col-6 mt-3 ms-3">
          <div className="fs-4 fw-bold pb-2">{this.state.title}</div>
          <div className="text-start">
            {(this.state.description && this.state.description.value
              ? this.state.description.value
              : this.state.description) || "Sorry, we don't have this book's description. Go by link below the cover image for more information."}
          </div>
        </div>

        <div className="col-3 mt-3">
          <div>
            <img
              className={s.authorPhoto + ' pb-3'}
              src={`https://covers.openlibrary.org/b/id/${this.state.authorPhotoId}-M.jpg`}
              alt="Sorry, there is no author photo"
            />
          </div>
          <div className="fs-4">Author: {this.state.authorName}</div>
        </div>
      </div>
    );
  }
}

export default withRouter(AboutBook);
