import axios from "axios";
import React, { Component } from "react";
import { withRouter } from "react-router";
import Book from "../content/book/book";
import s from "./searchResult.module.css";

class SearchResult extends Component {
  state = {
    books: [],
  };

  getBooks(){
    axios
      .get(` http://openlibrary.org/search.json?q=${this.props.match.params.query}`)
      .then((result) => result.data)
      .then((data) => {
        console.log(data);
        let arr = new Array(Math.min(20, data.docs.length)).fill(1);
        let resArr = arr.map((item, index) => ({
          imgUrl: `https://covers.openlibrary.org/b/id/${data.docs[index].cover_i}-L.jpg`,
          title: data.docs[index].title,
          bookId: data.docs[index].key.match(/\d+/g).join(""),
        }));
        this.setState({
          books: [...this.state.books, ...resArr],
        });
      })
      .catch();
  }

  componentDidUpdate(){
    
  }

  componentDidMount() {
    this.getBooks()
  }

  render() {
    return (
      <>
        <div className='fs-5'>Search results</div>
        <div className={s.content}>
          {this.state.books.map((item, index) => (
            <Book key={index} data={item} />
          ))}
        </div>
      </>
    );
  }
}

export default withRouter(SearchResult);
